import { useSelector } from "react-redux";
import { RootReduxState } from "../../../redux/store";
import classes from "./../Dialogs.module.css";
import avatar from "../../../assets/images/ava.jpg";
import { useParams } from "react-router-dom";

type propsType = {
  message: string;
  name: string;
  id: number;
};

const Message = (props: propsType) => {
  const userID = useSelector<RootReduxState, number | null>((state) => state.auth.id);
  const userName = useSelector<RootReduxState, string | null>((state) => state.auth.login);
  let { id } = useParams<{ id: string }>();
  return (
    <div className={classes.message_wrap}>
      <div className={classes.arrow_box}>
        <h3>{props.id !== userID ? props.name : userName}</h3>
        <p>{props.message}</p>
      </div>
      <div className={classes.message_avatar}>
        <img src={avatar} alt="" />
      </div>
    </div>
  );
};

export default Message;
