import { DialogPageType } from "./types";

let initialState = {
  dialogs: [
    { id: 1, name: "Pasha" },
    { id: 2, name: "Sasha" },
    { id: 3, name: "Alexej" },
    { id: 4, name: "Dima" },
    { id: 5, name: "Shizofreniya" },
    { id: 6, name: "freedom_Belarus" },
  ],
  messages: [
    { id: 1, name: "Pasha", message: "Hi" },
    { id: 2, name: "Sasha", message: "How r u" },
    { id: 3, name: "Alexej", message: "What is ur name" },
    { id: 4, name: "Dima", message: "Любишь пёсиков? =)" },
    { id: 5, name: "Shizofreniya", message: "London is a capital of great Britain" },
    { id: 6, name: "freedom_Belarus", message: "Live Belarus!" },
  ],
};

export const dialogsPageReducer = (state: DialogPageType = initialState, action: ActionsType) => {
  switch (action.type) {
    case "SEND_MESSAGE": {
      let body = action.newMessage;
      return {
        ...state,
        messages: [...state.messages, { id: 5740, message: body }],
      };
    }
    default:
      return state;
  }
};

type ActionsType = ReturnType<typeof sendMessageCreator>;

export const sendMessageCreator = (newMessage: string) => {
  return {
    type: "SEND_MESSAGE",
    newMessage,
  } as const;
};
